body{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: #5372f0;
}

.form-container {
  width: 605px;
  background: lightslategray;
  border: 1px solid black;
  border-radius: 15px;
  padding: 30px 30px 25px;
}

.form-input{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 23px;
  padding: 5px;
  color: #fff;
}

.form-input input{
  padding: 5px;
  margin-top: 7px;
  border: none;
  border-radius: 5px;
}

.btn{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 270px;
  padding: 8px;
  background-color: whitesmoke;
  border: none;
  border-radius: 10%;
  cursor: pointer;
}

.btn:hover{
  background-color: lightcyan;
}

.showDataStyles{
  background: white;
  border: 1px solid black;
  border-radius: 15px;
  padding: 10px 10px 10px;
  margin-top: 10px;
}